import React from "react";
import styled from "styled-components";
import { Box, Avatar } from "@mui/material";
import { BasicCard } from "../Mobile Components/Cards/Card";
import { SocialContainer } from "../../1 introduction/c1 - Left/c1c2 - Bottom/c1c2c3 - Right/c1c2c3c1to3 - Social/socialContainer";

const IntroStyler = styled(Box)`
  display: flex;
  flex: 1;
  flex-direction: column;
  background-color: purple;
  color: white;
  width: 100%;
  max-width: 100%;
`;

const Top = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-grow: 9;
  color: white;
`;

const C1Left = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(71, 72, 97);
  max-width: 20vw;
`;

const C2Right = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 2;
  background-color: purple;
  height: 100%;
  overflow: scroll;
`;

const Bottom = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(101, 41, 138);
`;

const BottomImg = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-grow: 1;
  border-radius: 20px;
  min-height: 20vh;
  height: 100%;
  background-color: rgb(101, 41, 138);
  justify-content: flex-end;
`;

const ImageWrapper = styled.div`
  flex: 1;
  margin: 10px;
  background-image: url("https://i.ibb.co/Tv1QdYs/tree.png");
  background-size: cover;
  background-position: center;
  border-radius: 10px;
`;

const ImageWrapper2 = styled.div`
  flex: 1;
  margin: 10px;
  background-image: url("https://i.ibb.co/cXqd54L/friends.jpg");
  background-size: cover;
  background-position: center;
  border-radius: 10px;
`;

const ImageWrapper3 = styled.div`
  flex: 1;
  margin: 10px;
  background-image: url("https://i.ibb.co/9T6TBn7/vista.jpg");
  background-size: cover;
  background-position: center;
  border-radius: 10px;
`;

const ImageWrapper4 = styled.div`
  flex: 1;
  margin: 10px;
  background-image: url("https://i.ibb.co/cYH6w3s/IMG-3479.png");
  background-size: cover;
  background-position: center;
  border-radius: 10px;
`;

export const MobileIntro = () => {
  const imgPath = "https://i.ibb.co/93GBhLm/Luke-Schmidt-Avatar.jpg";

  return (
    <IntroStyler>
      <Top>
        <C1Left>
          <Avatar
            alt="Luke Schmidt"
            src={imgPath}
            sx={{
              width: "100px",
              height: "100px",
              border: "1px solid black",
              alignSelf: "center",
            }}
          />
          <SocialContainer />
        </C1Left>
        <C2Right>
          <BasicCard
            header={"Luke Schmidt"}
            secondaryT={"Professional Portfolio"}
            secondaryU={"Computer Science BS"}
          >
            {" "}
            <br />
            <br />- Passionate Computer Science student at UNC Chapel Hill
            pursuing a BS degree with a strong foundation in coding languages
            such as Java, JavaScript, Python, and C#.
            <br />
            <br />- Skilled in developing robust applications using React/React
            Native frameworks and Unity technologies.
            <br />
            <br />- Successfully designed and launched a mobile application,
            showcasing a blend of technical proficiency and creative
            problem-solving abilities.
            <br />
            <br />- With strong experience in team management, I excel at
            coordinating project timelines to foster seamless collaboration.
            <br />
            <br />- Eager to leverage skills and knowledge to drive innovation
            in software development for a dynamic organization.
            <br />
            <br />
            <a href="https://fuchsia-giuditta-7.tiiny.site/">
              Check out my Resume
            </a>
          </BasicCard>
        </C2Right>
      </Top>
      <Bottom>
        <BottomImg>
          <ImageWrapper />
          <ImageWrapper2 />
          <ImageWrapper3 />
          <ImageWrapper4 />
        </BottomImg>
      </Bottom>
    </IntroStyler>
  );
};
